import React from 'react';
import ReactDOM from 'react-dom';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

import { Provider } from 'react-redux';
import configureStore from './redux/store/';

// Containers
import Main from './containers/Main'
import ShopDetail from './containers/ShopDetail'
import ShopCabinet from './containers/ShopCabinet'
import ViberAuthenticationComponent from './containers/ViberLogin'
import Header from './containers/HeaderContainer'
import AccountAdsContainer from './containers/AccountAdsContainer'

const store = configureStore()

ReactDOM.render(
  <Provider store={store}>
    <Router>
      <div classname='app' id="page">
        <Header />
        {/* <div className='footer'>
          <p className='foot-title'> </p>
        </div> */}
        <div className="page-content header-clear">
          <Switch>
            <Route exact path="/">
              <Main />
            </Route>
            <Route exact path="/dm/categories/:catName/">
              <Main />
            </Route>
            <Route path="/dm/shops/:shopId/products/:catName" component={ShopDetail} />
            <Route path="/dm/shops/my/" component={ShopCabinet} />
            {/* <Route path="/dm/account/ads/:shopId/">
              <AccountAdsContainer />
            </Route>  */}
            <Route path="/dm/login/v/:token" component={ViberAuthenticationComponent} />
          </Switch>
        </div>
      </div>
    </Router>
  </Provider>, document.getElementById('root')
);