import React, { Component, useState } from 'react';
import { connect } from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';

// actions
import ShopActions from '../redux/redux-sauce/shops';
import ProductsActions from '../redux/redux-sauce/products';

// form
import { InitShopForm, EditShopForm } from '../components/ShopForms';
import { CreateProductForm, UpdateProductForm } from '../components/ProductForms';


function ProductContent (props) {
  const { product } = props

  let btnClass = 'btn btn-s mb-1 mx-1 rounded-s text-uppercase font-500 font-13 shadow-s \
     bg-blue2 text-wrap'

  return(
    [
      <div className='content mb-0' key={1}>
        <p>{product.title}</p>
        <p>{product.price}</p>
        <p>{product.description}</p>
        <p>{product.active ? '' : ''}</p>
      </div>,
      <div className='content mt-1 mb-0' key={2}>
        <button className={btnClass} onClick={props.clickEdit} data-id={product.id}>
            
        </button>
        <button className={btnClass} onClick={() => props.deleteProduct(product.id)}>
            
        </button>
        <button className={btnClass} onClick={props.clickActive} data-id={product.id} 
          data-active={!product.active}>
            {product.active 
              ? '  '
              : ' '
            }
        </button>
      </div>
    ]
  )
}

function ProductEdit (props) {
  const { product, editId, categories, clickEdit, clickEditDone, clickActive, eventFetching,
       deleteProductImage, addProductImage, deleteProduct } = props
  let category

  Object.keys(categories).forEach(key => {

    if ((categories[key].name == product.category) | (categories[key].id == product.category))
      category = categories[key]
  })
  
  let edit = false
  if (product.id == editId) edit = true

  return (
    edit 
    ? <UpdateProductForm 
        initialValues={{
          id: product.id,
          title: product.title,
          price: product.price,
          category: category.id,
          description: product.description
        }}
        parentSubmit={clickEditDone}
        clickEdit={clickEdit}
        categories={categories}
      />
    : <div className='card card-style mt-2 mb-3 mx-1'>
        <ProductContent 
          product={product} 
          clickEdit={clickEdit} 
          clickActive={clickActive} 
          deleteProduct={deleteProduct}/>

        <div className='content mb-1' key={3}>
          {eventFetching 
            ? <CircularProgress />
            : product.images.length > 0 
                && product.images.map(image =>
                      <div className='rounded-m img-fluid mx-2 my-2 float-left'
                        key={image.id} style={{"height": "75px"}}
                        >
                          <img src={'http://35.222.169.29' + image.catalog_image} style={{"height": "75px"}}/>
                          <span className='badge badge-primary' 
                            onClick={() => deleteProductImage({image: image.id, id: product.id})}>
                            X
                          </span>
                      </div>
                      )
            }
          <div className="clearfix"></div>
          
          <div className="file-data pb-4 mb-4">
            <input className='upload-file bg-highlight shadow-s rounded-s bg-blue2-light'
              placeholder=' ' type='file' onChange={addProductImage} 
              data-product-id={product.id} />
            <p className="upload-file-text color-white"> </p>
          </div>
        </div>
      </div>
  )
}

class ProductsEdit extends Component {
  constructor(props) {
    super(props);  
    this.state = {
      editList: [],
      editId: null,
      needToRefresh: false
    }
    this.clickEdit = this.clickEdit.bind(this);
    this.clickEditDone = this.clickEditDone.bind(this);
    this.clickActive = this.clickActive.bind(this);
    this.addProductImage = this.addProductImage.bind(this);
  }

  clickEdit (e) {
    const { id } = e.target.dataset
    this.setState({
      ...this.state, 
      editId: id
    })
  }

  clickEditDone () {
    this.props.submitUpdateProduct()
    this.setState({
      ...this.state, 
      editId: null,
    })
  }

  clickActive (e) {
    this.props.submitActivateProduct(e.target.dataset)
  }

  addProductImage(e) {
    this.props.addProductImage({original: e.target.files[0], id: e.target.dataset.productId})
  }

  render () {
    const { products, eventFetching } = this.props
    return (
      products && products.length > 0 
      ? products.map(product => 
        <ProductEdit 
          product={product}
          editId={this.state.editId}
          clickEdit={this.clickEdit}
          clickEditDone={this.clickEditDone}
          clickActive={this.clickActive}
          clickDelete={this.props.clickDelete}
          categories={this.props.categories}
          eventFetching={eventFetching}
          addProductImage={this.addProductImage}
          deleteProductImage={this.props.deleteProductImage}
          deleteProduct={this.props.deleteProduct}
        />
        )
      : <p> </p>
    )
  }
}

function ShopInfoEditable (props) {
  const [edit, setEdit] = useState(false)
  const [createNewProduct, setCNP] = useState(false)
  const { shop, categories, submitUpdateProduct, submitActivateProduct, eventFetching } = props

  function createAndCloseform () {
    setCNP(!createNewProduct)
    props.createProductSubmit()
  }

  return (
    <div>
      {edit
        ? <EditShopForm 
            initialValues={
            {name: shop ? shop.name : null,
              description: shop ? shop.description : null ,
              delivery:  shop ? shop.delivery : null,
              id:  shop ? shop.id : null,
              phone:  shop ? shop.phone : null
            }}  
            parentSubmit={props.parentSubmit}
            setEdit={setEdit}
            />
        : <div className='card card-style mt-2 mx-1 mb-1'>
            <div className='content'>
              <h5 className='font-16'>{shop.name}</h5>
              <h5 className='font-14'>{shop.phone}</h5>
              <p className='font-13 mb-0'>{shop.description}</p>
              <p className='font-13 mb-0'>{shop.delivery}</p>
              <button className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
                bg-blue2' onClick={() => setEdit(true)}></button>
            </div>
          </div>
      }
      <div className='card card-style mx-1 my-2 rounded-s text-uppercase font-700 rounded-m text-center'
         onClick={() => setCNP(!createNewProduct)}>
           <div className='content'>
              {createNewProduct 
                ? ''
                : ' '
              }
            </div>
      </div>

      {createNewProduct &&
        <CreateProductForm 
            categories={categories}
            // parentSubmit={props.createProductSubmit}
            parentSubmit={createAndCloseform}
            initialValues={{}}
          />
      }

      <ProductsEdit 
        products={shop.products} 
        categories={categories} 
        submitUpdateProduct={submitUpdateProduct}
        submitActivateProduct={submitActivateProduct}
        deleteProduct={props.deleteProduct}
        eventFetching={eventFetching}
        addProductImage={props.addProductImage}
        deleteProductImage={props.deleteProductImage}
      />
      
    </div>
  )
}

class ShopCabinet extends Component {
  constructor(props) {
    super(props);  
    this.state = {
    }

  this.submitCreateShop = this.submitCreateShop.bind(this);
  this.submitUpdateShop = this.submitUpdateShop.bind(this);
  this.submitCreateProduct = this.submitCreateProduct.bind(this);
  this.submitUpdateProduct = this.submitUpdateProduct.bind(this);
  this.submitActivateProduct = this.submitActivateProduct.bind(this);
  }

  componentDidMount() {
    this.props.getCategories()
  }

  submitUpdateShop(){
    this.props.updateShop(this.props.state.form.editShopForm.values)
  }

  submitCreateShop(){
    this.props.createShop(this.props.state.form.initShopForm.values)
  }

  submitCreateProduct(){
    this.props.createProduct(this.props.state.form.createProductForm.values)
  }

  submitUpdateProduct(){
    this.props.updateProduct(this.props.state.form.updateProductForm.values)
  }

  submitActivateProduct(data){
    this.props.updateProduct({id: data.id, active: data.active})
  }

  render() {
    const { user, shop, categories } = this.props
    const { eventFetching } = this.props.state.shops
    console.log(this.props.state)
    return (
      user && !user.shop 
        ? <InitShopForm  parentSubmit={this.submitCreateShop} user={user}
          initialValues={{name: user ? user.nickname : null, profile_id: user ? user.id : null}}/>
        : shop && 
          <ShopInfoEditable 
            shop={shop}
            user={user}
            getShopDetail={this.props.getShopDetail}
            parentSubmit={this.submitUpdateShop}

            getShopDetail={this.props.getShopDetail}
            createProductSubmit={this.submitCreateProduct}
            categories={categories}
            submitUpdateProduct={this.submitUpdateProduct}
            submitActivateProduct={this.submitActivateProduct}
            deleteProduct={this.props.deleteProduct}

            addProductImage={this.props.addProductImage}
            deleteProductImage={this.props.deleteProductImage}

            eventFetching={eventFetching}
           />
    );
  }
}

const mapStateToProps = (state) => ({
  state: state,
  routing: state.routing,
  user: state.auth.user,
  shop: state.shops.shopOwn,
  categories: state.products.categories
})

const mapDispatchToProps = (dispatch) => ({
  getShopDetail: id => dispatch(ShopActions.getShopDetailRequest(id)),
  createShop: (data) => dispatch(ShopActions.createShopRequest(data)),
  updateShop: (data) => dispatch(ShopActions.updateShopRequest(data)),

  createProduct: (data) => dispatch(ShopActions.createProductRequest(data)),
  updateProduct: (data) => dispatch(ShopActions.partialUpdateProductRequest(data)),
  deleteProduct: id => dispatch(ShopActions.deleteProductRequest(id)),

  addProductImage: (data) => dispatch(ShopActions.addProductImageRequest(data)),
  deleteProductImage: (data) => dispatch(ShopActions.deleteProductImageRequest(data)),

  getCategories: query => dispatch(ProductsActions.getCategoriesRequest(query)),

})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ShopCabinet);
