import React, { Component, useState  } from 'react';
import { Field, reduxForm } from 'redux-form'

import TextField from '@material-ui/core/TextField'
import Checkbox from '@material-ui/core/Checkbox'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import Select from '@material-ui/core/Select'
import InputLabel from '@material-ui/core/InputLabel'
import FormHelperText from '@material-ui/core/FormHelperText'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'

import { SubmissionError } from 'redux-form'

import { renderFromHelper, renderTextField, renderSelectField } from './ShopForms'


function ProductForm (props) {
  const { categories } = props

  return (
    <div>
      <Field
          component={renderTextField}
          name="title"
          label=''
          className='input-group my-2'
          labelClass='font-12'
        />

      <Field
          component={renderTextField}
          name="price"
          label='   '
          className='input-group my-2'
          labelClass='font-12'
        />
      
      <Field 
          name="category" 
          component={renderSelectField}
          labelClass='font-12 '
          formClass='input-group my-2'
          label='  *'
         >
            {categories && categories.length > 0 && categories.map(category => 
              <option className='font-10' value={category.id} key={category.id}>{category.ru_name}</option>
              )}
        </Field>
      
      <Field 
        name="description" 
        component={renderTextField} 
        className="form-control font-12 my-2 "
        style={{"min-height": "150px"}} 
        variant="outlined"
        labelClass={'font-12'}
        multiline
        rowsMax={6}
        label='()'
        />

      <Field
        component={renderTextField}
        name="shop_id"
        hidden={true}
      />

      <Field
        component={renderTextField}
        name="id"
        hidden={true}
      />
    </div>
  )
}

export function CreateProductForm (props) {
  const { parentSubmit, pristine, reset, submitting, handleSubmit, initialValues, categories } = props
  return (
    <div className='card card-style mx-1 mb-1 mt-2'>
      <div className='content'>
        <h3 className='font-18 font-500 text-center'> </h3>
        <form onSubmit={handleSubmit(parentSubmit)}>
          <ProductForm initialValues={initialValues} categories={categories}/>
          <button 
            className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
            bg-blue2 text-wrap'
            type="submit"
            disabled={pristine || submitting}>
            
          </button>
        </form>
      </div>
    </div>
  )
}

const validateProductForm = values => {
  const errors = {}
  const requiredFields = [
    'title',
    'category',
    'price'
  ]
  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = ' '
    }
  })
  return errors
}

CreateProductForm = reduxForm({
  form: 'createProductForm',
  validate: validateProductForm,
})(CreateProductForm)


export function UpdateProductForm (props) {
  const { parentSubmit, pristine, reset, submitting, handleSubmit, initialValues, categories } = props
  return (
    <form onSubmit={handleSubmit(parentSubmit)}>
      <div className='card card-style mx-1 mb-1 mt-2'>
        <div className='content mb-0'>
          <ProductForm initialValues={initialValues} categories={categories}/>
        </div>
        <div className='content mt-1'>
          <button 
            className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
            bg-blue2 text-wrap'
            type="submit"
            disabled={pristine || submitting}>
            
          </button>
          <button className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
            bg-blue2 text-wrap' onClick={props.clickEdit} data-id={null}>
              
          </button>
        </div>
      </div>
    </form>
  )
}

UpdateProductForm = reduxForm({
  form: 'updateProductForm',
  validate: validateProductForm,
})(UpdateProductForm)