import axios from 'axios';
import endpoints from './endpoints';
import { parseErrorData, createUrlParamsFromFilters } from './utils';

const create = () => {

    const getShops = (filters) => {
        const params = createUrlParamsFromFilters(filters);
        return axios.get(endpoints.GET_SHOPS, { params })
        .then(response => response.data)
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const getShopDetail = id => {
        const token = localStorage.getItem('token') || '';
        const url = endpoints.getShopDetail(id);

        return axios({
                    method: 'get',
                    url: url,
                    // headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const createShop = payload => {
        const { name, phone, description, delivery, profile_id } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.GET_SHOPS;

        const formData = new FormData();
        formData.append("name", name);
        formData.append("phone", phone);
        formData.append("profile", profile_id);
        description && formData.append("description", description);
        delivery && formData.append("delivery", delivery);
        
        return axios({
                    method: 'post',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const updateShop = payload => {
        const { id, name, description, delivery, phone } = payload;
        const token = localStorage.getItem('token') || '';
        const url = endpoints.updateShop(id);
    
        const formData = new FormData();
        name && formData.append("name", name);
        description && formData.append("description", description);
        phone && formData.append("phone", phone);
        delivery && formData.append("delivery", delivery);
    
        return axios({
                    method: 'patch',
                    url: url,
                    data: formData,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    const likeShop = id => {
        const token = localStorage.getItem('token') || '';
        const url = endpoints.likeShop(id);
        console.log('id', id)
        return axios({
                    method: 'post',
                    url: url,
                    headers: { 'content-type': 'multipart/form-data', 'Authorization': `JWT ${token}` }
        })
        .then(response => {
            return response.data
        })
        .catch(err => {
            const error = new Error(err);
            error.data = parseErrorData(err);
            throw error;
        })
    }

    return {
        getShops,
        getShopDetail,
        createShop,
        updateShop,
        likeShop
    }
}

export default {
    create
}