import React, { Component } from 'react';
import { connect } from 'react-redux';
import { useHistory, useParams, Link } from 'react-router-dom'
import { url } from '../redux/api/endpoints';
import { Helmet } from 'react-helmet';

// actions
import ProductsActions from '../redux/redux-sauce/products';
import ShopsActions from '../redux/redux-sauce/shops';

// import Background from '../../../images/icons/6.png';


export function CatButtonComp (props) {
  const { cat, className, handleClick } = props

  return (
    <button 
      className={className}
      key={cat.id} onClick={handleClick} 
      data-id={cat.id}
      >
        {cat.ru_name} 
        {cat.images.length > 0 && <img style={{'width': '25px'}} 
          src={'http://35.222.169.29/' + cat.images[0].catalog_image} />}
    </button>
  )
}

function CatButton(props) {
  let history = useHistory();
  const { catName } = useParams();
  const { cat } = props

  if (!catName){
    history.push("/dm/categories/" + cat.name);
  }

  const className = 
    'btn btn-xs mb-1 ml-1 rounded-xl font-500 shadow-s text-wrap float-left'

  const classActive = catName == cat.name 
    ? className + ' bg-magenta2-light' 
    : className + ' ' 

  function handleClick() {
    history.push("/dm/categories/" + cat.name);
    props.getShops({category: cat.name})
  }

  return (
    <CatButtonComp
      className={classActive}
      cat={cat} 
      handleClick={handleClick} 
    />
  );
}

function ShopsList(props) {
  const { catName } = useParams();
  const { shop_list, listFetching, errorList } = props

  if (catName && shop_list.length <= 0 && !listFetching && !errorList) {
    props.getShops({category: catName})
  }

  return (
    !listFetching && shop_list && shop_list.length > 0 && shop_list.map(shop => 
      <Link to={'/dm/shops/' + shop.id + '/products/' + catName}>
        <div className='card card-style mx-1 mb-2 pb-2 px-1'>
          <div className='content mb-0'>
            <h5 className='font-17 float-left'>{shop.name}</h5>
            <p className='float-right'>
              <i className="fas fa-heart color-red2-light font-20" aria-hidden="true">
                <sup className='font-700'>{shop.likes_list.length}</sup>
              </i>
              <i className="far fa-comments ml-3 font-20" aria-hidden="true">
                <sup className='font-700'>{shop.feedbacks_list.length} </sup>
              </i> 
            </p>
          </div>
          <div className='content mt-0 mb-1'>
            {/* <p className='my-1'>
              {shop.description}
            </p> */}
            <div className='' style={{'line-height': '15px'}}>
              {shop.category_products && shop.category_products.length > 0 && shop.category_products.map(product => 
                <div className='item-product'>
                    <p className='item-product-title'>{product.title} - {product.price}</p>
                </div>
                )}
            </div>
          </div>
          <div className='content mt-1'>
            {shop.category_products && 
                <img src={url + shop.category_images[0]} className='rounded-s shop-list-img'
                />
              } 
          </div>
        </div>
      </Link>
    )
  );
}

class Main extends Component {
  constructor(props) {
    super(props);
    this.state = {
    }
  }

  componentDidMount() {
    this.props.getCategories()
  }

  render() {
    const { categories } = this.props.state.products
    const { shop_list, listFetching, errorList } = this.props.state.shops

    return (
      <div>
        <Helmet>
          <title>Ed Sheeran</title>
          <meta property="og:title" content="I see fire"/>
          <meta property="og:description" content="inside a mountains" /> 
          <meta property="og:image" content={`/images/products/3.png`} />
          <meta property="theme-color" content="#008f68" />
        </Helmet>
        {/* <meta property={{og:image content=/small-im.png /> */}
        <div className='card card-style rounded-m mx-1 my-1 pl-0 cats-block ' 
          >
          <div className='content my-2 mx-1'>
            {categories && categories.map(cat =>
              <CatButton cat={cat} getShops={this.props.getShops}/>
            )}
          </div>
          <div className='clearfix'></div>
        </div>

        <ShopsList shop_list={shop_list} listFetching={listFetching} getShops={this.props.getShops}
          errorList={errorList}/>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  notifications: state.notifications,
  state: state
})

const mapDispatchToProps = (dispatch) => ({
  getCategories: query => dispatch(ProductsActions.getCategoriesRequest(query)),
  getShops: query => dispatch(ShopsActions.getShopsRequest(query)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Main);
