import React, { Component, useState  } from 'react';
import { Field, reduxForm } from 'redux-form'

import TextField from '@material-ui/core/TextField'
import FormControl from '@material-ui/core/FormControl'
import Select from '@material-ui/core/Select'
import InputLabel from '@material-ui/core/InputLabel'
import FormHelperText from '@material-ui/core/FormHelperText'


export const renderFromHelper = ({ touched, error }) => {
  if (!(touched && error)) {
    return
  } else {
    return <FormHelperText>{touched && error}</FormHelperText>
  }
}

export const renderTextField = ({
  label,
  placeholder,
  input,
  multiline,
  meta: { touched, invalid, error },
  labelClass,
  ...custom
}) => (
  <TextField
    label={label}
    placeholder={placeholder}
    error={touched && invalid}
    helperText={touched && error}
    {...input}
    {...custom}
    multiline={multiline}
    InputLabelProps={{
      className: labelClass,
    }}
  />
)

export const renderSelectField = ({
  input,
  label,
  formClass,
  labelClass,
  meta: { touched, error },
  children,
  ...custom
}) => (
  <FormControl error={touched && error} className={formClass}>
    <InputLabel className={labelClass}>{label}</InputLabel>
    <Select
      {...input}
      {...custom}
    >
      {children}
    </Select>
    {renderFromHelper({ touched, error })}
  </FormControl>
)

export const renderDateTimeField = ({
  label,
  placeholder,
  input,
  meta: { touched, invalid, error },
  labelClass,
  ...custom
}) => (

  <TextField
    label={label}
    type="date"
    error={touched && invalid}
    helperText={touched && error}
    {...input}
    {...custom}
    InputLabelProps={{
      className: labelClass,
      shrink: true 
    }}
  />
)

const validateShopForm = values => {
  const errors = {}
  const requiredFields = [
    'name',
    'phone',
  ]
  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = ' '
    }
  })
  return errors
}

function ShopForm (props) {
  const [delEnabled, setDel] = useState(false);

  return (
    <div>
      <Field
          component={renderTextField}
          name="name"
          label=' '
          className='input-group my-2'
          labelClass='font-12'
        />
      
      <Field
          component={renderTextField}
          name="phone"
          label=''
          className='input-group my-2'
          labelClass='font-12'
        />
      
      {delEnabled 
        ? <Field name="delivery" component="textarea" className="form-control font-12 my-2"
            style={{"height": "150px"}} label=''
            placeholder='     .
            '
            />
        : <button className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
        bg-blue2 text-wrap' onClick={() => setDel(!delEnabled)}>?</button>
      }

      <Field name="description" component="textarea" className="form-control font-12 my-2"
          style={{"height": "150px"}} label='. ()'
          placeholder='   ? 
          -   .
          -      ,       .
          -     ,   .
          '
          />
      <Field
        component={renderTextField}
        name="profile_id"
        hidden={true}
      />
      <Field
        component={renderTextField}
        name="id"
        hidden={true}
      />
    </div>
  )
}

export function InitShopForm (props) {
  const { parentSubmit, pristine, reset, submitting, handleSubmit, initialValues } = props
  return (
    <div className='card card-style mx-0 mb-1 mt-2'>
      <div className='content'>
        <form onSubmit={handleSubmit(parentSubmit)}>
          <ShopForm initialValues={initialValues}/>
          <button 
            className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
            bg-blue2 text-wrap'
            type="submit"
            disabled={pristine || submitting}>
            
          </button>
          
        </form>
      </div>
    </div>
  )
}

InitShopForm = reduxForm({
  form: 'initShopForm',
  validate: validateShopForm,
})(InitShopForm)


export function EditShopForm (props) {
  const { parentSubmit, pristine, reset, submitting, handleSubmit, initialValues } = props

  function submitAndClose(){
    props.setEdit(false)
    parentSubmit()
  }

  return (
    <div className='card card-style mx-0 mb-1 mt-2'>
      <div className='content'>
        <form onSubmit={handleSubmit(submitAndClose)}>
          <ShopForm initialValues={initialValues} phone={false}/>
          <button 
            className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
            bg-blue2 text-wrap'
            type="submit"
            disabled={pristine || submitting}>
            
          </button>
          <button 
            className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
            bg-blue2 text-wrap'
            onClick={() => props.setEdit(false)}
            >
            
          </button>
        </form>
      </div>
    </div>
  )
}

EditShopForm = reduxForm({
  form: 'editShopForm',
  validate: validateShopForm,
})(EditShopForm)


export function FeedbackCreateForm (props) {
  const { parentSubmit, pristine, reset, submitting, handleSubmit, initialValues } = props

  return (
    <div className='card card-style mx-0 mb-1 mt-2'>
      <div className='content'>
        <p className='mb-0'>  </p>
        <form onSubmit={handleSubmit(parentSubmit)}>
        <div>
          <Field name="text" component="textarea" className="form-control font-12 my-2"
            style={{"height": "150px"}} label=''
            />
          <Field
            component='number'
            name="shop"
            hidden={true}
          />
        </div>
          <button 
            className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
            bg-blue2 text-wrap'
            type="submit"
            disabled={pristine || submitting}>
            
          </button>
        </form>
      </div>
    </div>
  )
}

FeedbackCreateForm = reduxForm({
  form: 'feedbackCreateForm',
  // validate: validateShopForm,
})(FeedbackCreateForm)

export function FeedbackEditForm (props) {
  const { parentSubmit, pristine, reset, submitting, handleSubmit, initialValues, setEditOnOff } = props
  
  function submitAndClose(){
    setEditOnOff(false)
    parentSubmit()
  }

  return (
    <div className='card card-style mx-0 mb-1 mt-2'>
      <div className='content'>
        <form onSubmit={handleSubmit(submitAndClose)}>
        <div>
          <Field name="text" component="textarea" className="form-control font-12 my-2"
            style={{"height": "150px"}} label=''
            />
          <Field
            component='number'
            name="id"
            hidden={true}
          />
        </div>
          <button 
            className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
            bg-blue2 text-wrap'
            type="submit"
            disabled={pristine || submitting}>
            
          </button>
          <button 
            className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
            bg-blue2 text-wrap'
            onClick={() => setEditOnOff(false)}
          >
            
          </button>
        </form>
      </div>
    </div>
  )
}

FeedbackEditForm = reduxForm({
  form: 'feedbackEditForm',
  // validate: validateShopForm,
})(FeedbackEditForm)