import React, { Component } from 'react';
import { connect } from 'react-redux';
import { useHistory, useParams } from 'react-router-dom'

import { url } from '../redux/api/endpoints';
// actions
import ShopActions from '../redux/redux-sauce/shops';


class AccountAdsContainer extends Component {
  constructor(props) {
    super(props);  
    this.state = {
    }
  }

  componentDidMount() {
  }

  render() {
    
    return (
        <div className='container-fluid shop-detail'>
          <p>AccountAdsContainer</p>
          <div>
            {this.props.children}
          </div>
        </div>
    );
  }
}

const mapStateToProps = (state) => ({
  state: state,
  routing: state.routing,
})

const mapDispatchToProps = (dispatch) => ({
  getShopDetail: id => dispatch(ShopActions.getShopDetailRequest(id)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(AccountAdsContainer);
